@import '~assets/styles/h3Imports';

.root {
  --icon-fill: var(--brand-primary, #e52600);

  display: flex;

  &:first-of-type {
    margin-bottom: em(24);
  }

  &:last-of-type {
    margin-bottom: em(32);
  }
}

.text {
  display: flex;
  flex-direction: column;
  margin-left: em(8);

  span {
    &:first-of-type {
      margin: em(4) 0;
    }
  }

  .text-info {
    color: var(--gray-3);
    font-size: rem(14);
  }
}
